a {
  color: white;
  text-decoration: none;
}
.App {
  text-align: center;
}

.App-header {
  background-color: #232b3a;

  padding: 20px 30px;
  color: white;
}
.App-header nav {
  width: 100%;
  display: grid;
  grid-template-columns: auto auto 1fr;
  grid-gap: 20px;
}

.App-header nav a {
  font-size: 20px;
}
.login-out {
  justify-self: flex-end;
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
